<template>
  <section class="new-announcement">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">اطلاعیه ها</h4>
      </div>
      <span>لطفا برای دریافت صورتحساب ابتدا نوع آن را مشخص کنید </span>
    </div>

    <main>
      <b-card>
        <!-- title -->
        <b-form-group label-for="name" label="عنوان اطلاعیه">
          <b-form-input id="name" type="text" v-model="titleMessage" />
        </b-form-group>

        <!-- date -->
        <b-form-group label-for="name" label="تاریخ انتشار">
          <b-row>
            <b-col>
              <v-select :searchable="false" :clearable="false" v-model="selectedDay" dir="rtl" :options="days" class="select-size-lg" label="debt_category_name">
                <template #option="{ debt_category_name }">
                  <span class="d-flex f16"> {{ debt_category_name }}</span>
                </template>
              </v-select>
            </b-col>

            <!-- month -->
            <b-col>
              <v-select :searchable="false" :clearable="false" v-model="selectedMonth" :options="months" dir="rtl" class="select-size-lg" label="debt_category_name">
                <template #option="{ debt_category_name }">
                  <span class="d-flex f16"> {{ debt_category_name }}</span>
                </template>
              </v-select>
            </b-col>

            <!-- year -->
            <b-col>
              <v-select :searchable="false" :clearable="false" v-model="selectedYear" :options="years" dir="rtl" class="select-size-lg" label="debt_category_name">
                <template #option="{ debt_category_name }">
                  <span class="d-flex f16"> {{ debt_category_name }}</span>
                </template>
              </v-select>
            </b-col>
          </b-row>
        </b-form-group>

        <!-- textarea  -->
        <b-form-group label-for="new_announcement_desc" label="توضیحات">
          <b-form-textarea id="new_announcement_desc" rows="10" v-model="bodyMessage" />
        </b-form-group>

        <!-- submit btn -->
        <div class="btn-action">
          <b-button block variant="primary" @click="store()"> {{ announcement ? 'ویرایش' : 'ارسال' }} اطلاعیه </b-button>
        </div>
      </b-card>
    </main>
  </section>
</template>

<script>
import vSelect from "vue-select";
import { getTime } from '@/api/generalServices';
import { getMessage, storeMessage, updateMessage } from "/src/api/messageServices";
import { BCard, BAvatar, BButton, BFormGroup, BFormInput, BRow, BCol, BFormTextarea } from 'bootstrap-vue';
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue"
export default {
  name: "addOrEditAnnouncement",
  components: {
    BCard,
    BAvatar,
    BButton,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    vSelect,
    BFormTextarea,
    ToastificationContent,
    getTime
  },
  data() {
    return {
      selectValue: null,
      titleMessage: null,
      bodyMessage: null,
      selectedDay: null,
      selectedMonth: null,
      selectedYear: null,
      years: [],
      months: (() => { return [...[...Array(12).keys()].map(i => i + 1)].map(k => k < 10 ? `0${k}` : k) })(),
      days: (() => { return [...[...Array(31).keys()].map(i => i + 1)].map(k => k < 10 ? `0${k}` : k) })(),
    }
  },
  methods: {
    async store() {

      /* validation */
      if (!this.titleMessage) return this.toast("فیلد عنوان الزامی است")

      if (!this.bodyMessage) return this.toast("فیلد توضیحات الزامی است")

      /**store the announcement */
      try {
        let data = {
          title: this.titleMessage,
          body: this.bodyMessage,
          date: `${this.selectedYear}/${this.selectedMonth}/${this.selectedDay}`,
        }

        if(this.announcement){
          data.id= this.announcement.id
        }

        if(!this.announcement) await storeMessage(data)

        if(this.announcement) await updateMessage(data)
        
        this.toast("سرفصل با موفقیت ذخیره شد", "success", "CheckSquareIcon")
        
        this.$router.push({ name: "Announcement" })
      } catch (error) {
        console.error(error)
      }
    },

    /* get time  and show in v-select */
    async getTime() {
      try {
        const { data } = await getTime()
        const dt = String(data.locale_date).split('/')

        // dafault day 
        this.selectedDay = dt[2]

        /**get jalali years 4 years ahead and 4 years behind */
        this.years = (() => {
          const currentYear = parseInt(dt[0]);
          const yearsAhead = 4;
          const yearsBehind = 4;

          const years = [];
          for (let i = -yearsBehind;i <= yearsAhead;i++) {
            years.push(currentYear + i);
          }
          return years
        })()

        /**default month */
        this.selectedMonth = dt[1]

        /**default year */
        this.selectedYear = dt[0]
      } catch (error) {
        console.error(error)
      }
    },

    /* get data by id for update */
    async getAnnouncement() {
      let id = this.$route.params.id;
      let { data } = await getMessage(
        {
          params: {
            'id': id
          }
        }
      );
      this.titleMessage = data.data.data[0].message_title;
      this.bodyMessage = data.data.data[0].message_body;
    },

    /**
     * less code
     * @param {String} title 
     * @param {String} variant 
     * @param {String} icon 
     */
    toast(title, variant = "danger", icon = "AlertCircleIcon") {
      return this.$toast({
        component: ToastificationContent,
        props: {
          title: title,
          icon: icon,
          variant: variant,
        },
      })
    },
  },
  async mounted() {
    /* excute getTime */
    this.getTime();

    // Disabled, no show api present by server 
    // if (this.$route.params.id) {
    // this.getAnnouncement();
    // }

    if (this.announcement) {
      this.titleMessage = this.announcement.message_title;
      this.bodyMessage = this.announcement.message_body;
    }
  },
  computed: {
    announcement(){
      return this.$store.state.announcement.announcement
    }
  }
}
</script>

